@font-face {
    font-family: 'Roboto' ;
    src: url(/Bienes-raices/static/media/Roboto-Light.6090d256.ttf) format('truetype');
}
@font-face {
    font-family: 'Roboto-Bolder' ;
    src: url(/Bienes-raices/static/media/Roboto-Bold.9ece5b48.ttf) format('truetype');
}
h2, h3, h4, button, a, span{
    font-family: 'Roboto', sans-serif;
}
h1, p{
    font-family: 'Roboto-Bolder';
}
.headerInfo{
    margin: -6vh 1vw 0vh 1vw;
    background-color: white;
    position: fixed;
    z-index: 10;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.logoHeader{
    width: 12vw;
}
.headerInfo h2{
    font-size: 2.2vw;
    position: static;
    padding-left: 2vh;
}
.loupeHeader{
    position: relative;
    top: 0.8vh;
    padding-top: 4vh;
    width: 2.5%;
}
.headerInfo nav{
    text-align: end;
}
.headerInfo a{
    padding: 2vw;
    font-size: 1.4vw;
    text-decoration: none;
    color: black;
    font-weight: bolder;
}
.headerInfo button{
    padding: 1.3vw 5vw 1vw 5vw;
    border-radius: 20vw;
    font-size: 1.5vw;
    font-weight: bolder;
    background-color: white;
    border: 1px solid black;
}
.topicsHeader{
    margin-top: 5vh;
    padding: 5vw 14vw 0vw 4vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.topicsHeader a{
    text-decoration: none;
    font-size: 1.2vw;
    color: black;
    font-weight: bolder;
}
.categoryHeader{
    display: none;
}
@media screen and (max-width: 768px){
    .logoHeader{
        width: 22vw;
    }
    .headerInfo h2{
        position: absolute;
        right: 36vh;
        top: 2px;
        font-size: 2.5vh;
    }
    .headerInfo{
        background: white;
        position: fixed;
        width: 100%;
        z-index: 10;
    }
    .loupeHeader{
        width: 5vw;
    }
    .archivesHeader{
        display: none;
    }
    .tagsHeader{
        display: none;
    }
    .suscribeHeader{
        display: none;
    }
    .categoryHeader{
        display: inline;
        display: initial;
    }
    .headerInfo a{
        position: relative;
        right: 5vw;
        top: -4vw;
        font-size: 3.5vw;
        text-align: end;
    }
    .topicsHeader{
        padding: 0%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        text-align: center;
        position: relative;
        top: 5vh;
    }
    .topicsHeader a{
        font-size: 3.6vw;
        margin-bottom: 2vw;
    }

}



.content{
    display: -webkit-flex;
    display: flex;
    padding: 0 4vh 0 4vh;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.tubikBlog h1{
    font-size: 5vw;
    font-weight: lighter;
}
.tubikBlog img{
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
    width: 48vw;
    height: 52vw;
    border-radius: 10px;
}
@media screen and (max-width: 768px){
    .content{
        display: inline-block;
        padding: 3vh 1vh 0 1vh;
    }
    .tubikBlog h1{
        font-size: 7vw;
    }
    .tubikBlog img{
        -webkit-filter: brightness(60%);
                filter: brightness(60%);
        width: 100%;
        height: 50%;
        border-radius: 10px;
    }
}
.textImg{
    position: absolute;
    top: 42vh;
    padding: 5vh;
    width: 70vh;
    color: white;
}
.textImg h4{
    width: 30vh;
    font-size: 2vh;
    font-weight: bolder;
}
.textImg p{
    font-family: 'Roboto' ;
    font-size: 1.8vh;
    font-weight: bolder;
}
.textImg h2{
    font-family: 'Roboto-Bolder' ;
    font-size: 4vh;
}
@media screen and (max-width: 768px){
    .textImg{
        width: 70vw;
        position: absolute;
        top: 85vw;
    }
    .textImg h4{
        font-size: 3.2vw;
        font-weight: bolder;
    }
    .textImg p{
        font-size: 3.5vw;
        font-weight: bolder;
    }
    .textImg h2{
        font-family: 'Roboto-Bolder' ;
        font-size: 7vw;
    }
}
.viewLatest{
