    }
    .view{
        display: -webkit-flex;
        display: flex;
        margin: 0 1vh 0vh 0vh;
    }
    .viewContent{
        width: 32vw;
        margin: 0 5vh 5vh 1vh;
    }
}
.popularContent{
    color: white;
    margin: 13vh 3vh 0vh 3vh;
}
.popularContent h2{
